.third-card {
  position: relative;
  height: 380px;
  transform: scale(1);
  transition: transform 0.5s;
  border-radius: 10px;
  overflow: hidden;
}
.third-card:hover {
  transform: scale(1.01);
  transition: transform 0.5s;
}
.third-card .stamp {
  width: calc(100% - 45px);
}
.third-card .subzone, .third-card .subzone a {
  display: block;
  height: 100%;
}
.third-card #banner-middle {
  height: 100%;
}
.third-card .subzone a img {
  display: block;
  height: 100%;
  width: 100%;
}

@media (max-width: 1024px) {
  .third-card--50 {
    width: calc(50% - 16px);
  }
}
@media (max-width: 767px) {
  .third-card {
    width: 100%;
    height: 310px;
  }
}